<template>
	<div class="container" v-loading.body="loading" element-loading-text="拼命加载中">
		<p class="city-select-txt">选择客户类型</p>
		<el-tabs v-model="activeCustomer" type="card" @tab-click="handleClick">
	    <el-tab-pane label="全部成交客户" name="all"></el-tab-pane>
	    <el-tab-pane label="已创建客户" name="created"></el-tab-pane>
	    <el-tab-pane label="待创建客户" name="notCreated"></el-tab-pane>
  	</el-tabs>
		<p class="city-select-txt">条件筛选</p>
		<el-select v-model="value1" placeholder="按总成交次数筛选" @change="filterList($event, 'deal_times')">
			<el-option
	      key="0"
	      label="按总成交次数筛选"
	      value="0"
				>
	    </el-option>
			<el-option
	      v-for="item in filterData.deal_times"
	      :key="item.value"
	      :label="item.label"
	      :value="item.value"
				>
	    </el-option>
	  </el-select>
		<el-select v-model="value2" placeholder="按总成交金额筛选" @change="filterList($event, 'total_price')">
			<el-option
	      key="0"
	      label="按总成交金额筛选"
	      value="0"
				>
	    </el-option>
	    <el-option
	      v-for="item in filterData.total_price"
	      :key="item.value"
	      :label="item.label"
	      :value="item.value">
	    </el-option>
	  </el-select>
		<el-select v-model="value3" placeholder="按最近成交时间筛选" @change="filterList($event, 'deal_time')">
			<el-option
	      key="0"
	      label="按最近成交时间筛选"
	      value="0"
				>
	    </el-option>
	    <el-option
	      v-for="item in filterData.deal_time"
	      :key="item.value"
	      :label="item.label"
	      :value="item.value">
	    </el-option>
	  </el-select>
		<el-select v-model="value4" placeholder="按客户信息完善度筛选" @change="filterList($event, 'perfection_degree')">
			<el-option
	      key="0"
	      label="按客户信息完善度筛选"
	      value="0"
				>
	    </el-option>
	    <el-option
	      v-for="item in filterData.perfection_degree"
	      :key="item.value"
	      :label="item.label"
	      :value="item.value">
	    </el-option>
	  </el-select>


		<el-table
		 :data="customerData"
		 border
		 @sort-change="sortChange"
		 style="margin-top:20px;"
		 >
		 <el-table-column
			 prop="id"
			 label="客户ID"
			 >
		 </el-table-column>
		 <el-table-column
			 prop="name"
			 label="客户姓名"
			 >
			 <template scope="scope">
				 <!-- <a target="_blank" :href="/foreground/index/userview?from=boss&checkType=1&mobile=scope.row.mobile&companyId=scope.row.company_id"> -->
				 <a style="color:#409EFF" v-if="scope.row.is_create" target="_blank" :href="'/foreground/index/userview?from=bossAdd&checkType=2&mobile='+scope.row.mobile+'&companyId='+scope.row.company_id">
					 {{ scope.row.name }}
				</a>
				<div v-else>{{ scope.row.name }}</div>
			</template>
		 </el-table-column>
		 <el-table-column
			 prop="company"
			 label="所在公司"
			 width="300"
			 >
		 </el-table-column>
		 <el-table-column
			 prop="deal_times"
			 label="总成交次数"
			  width="130"
			 sortable='custom'
			 label-class-name="table-title">
		 </el-table-column>
		 <el-table-column
			 prop="total_price"
			 label="总成交金额"
			 width="130"
			 label-class-name="table-title"
			 sortable='custom'>
		 </el-table-column>
		 <el-table-column
			 prop="deal_time"
			 label="最近成交时间"
			 width="200"
			 label-class-name="table-title"
			 sortable='custom'>
		 </el-table-column>
		 <el-table-column
			 prop="deal_price"
			 label="最近成交金额"
			  width="150"
				label-class-name="table-title"
			 sortable='custom'>
		 </el-table-column>
		 <el-table-column
			 prop="perfection_degree"
			 label="客户信息完善比例"
			  width="180"
				label-class-name="table-title"
			 sortable='custom'>
		 </el-table-column>
		 <el-table-column
			 prop="deal_admins"
			 label="可查看专员（多人）"
			 width="180"
			 v-if="activeCustomer == 'created' || activeCustomer == 'notCreated'"
			 >
		 </el-table-column>
		 <el-table-column
			 prop="perfection_degree_admin"
			 label="信息完善贡献率最高专员"
			 width="200"
			 v-if="activeCustomer == 'created'"
			 >
		 </el-table-column>
		 <el-table-column
			 prop="contribution_rate"
			 label="贡献率占比"
			 v-if="activeCustomer == 'created'"
			 >
		 </el-table-column>

	 </el-table>

	 <el-pagination
		 @current-change="handleCurrentChange"
		 :page-size="10"
		 :current-page.sync="customerParam.page"
		 layout="total, prev, pager, next"
		 :total="pageParam.total"
		 style="margin-top:20px;"
		 >
	 </el-pagination>
	</div>
</template>

<script>


export default {
	data: function () {
		return {
			loading: true,
			activeCustomer: 'all',
			customerParam:{
				page: 1,
				type: 0,
			},
			pageParam: {},
			filterData:{
				deal_times:[],
				total_price:[],
				deal_time:[],
				perfection_degree:[]
			},
			value1: '',
			value2: '',
			value3: '',
			value4: '',
			customerData: []
    }
	},
	mounted(){
		var self = this;
		self.loading = true;
		self.getCustomerList();
		self.getFilterData();
	},
	methods: {
		handleClick(tab, event) {
			var self = this;
			var tabName = tab.name;
			var typeNum = {
					'all': 0,
					'created': 1,
					'notCreated': 2
			};
			self.resetFilterVal();

			self.customerParam.type = typeNum[tabName];
			self.customerParam.page = 1;
			self.loading = true;
			self.getCustomerList();
    },
		resetFilterVal() {
			var self = this;
			self.value1 = '';
			self.value2 = '';
			self.value3 = '';
			self.value4 = '';
		},
		handleCurrentChange(val) {
			var self = this;
			self.loading = true;
			this.getCustomerList();
		},
		getCustomerList() {
			var self = this;
			self.$ajax.get(self.$api.getModuleHost('CRM', "/manage/getCustomerList"),self.customerParam).then(function (data) {
				if (data.code == 1000) {
					self.customerData = data.data;
					self.pageParam['total'] = data.total;
					self.loading = false;
				}
			})
		},
		getFilterData() {
			var self = this;
			self.$ajax.get(self.$api.getModuleHost('CRM', "/manage/getFilterField")).then(function (data) {
				if (data.code == 1000) {
					var data = data.data;
					for (let key in data.deal_times){
						self.filterData['deal_times'].push({value: key, label: data.deal_times[key]})
					}
					for (let key in data.total_price){
						self.filterData['total_price'].push({value: key, label: data.total_price[key]})
					}
					for (let key in data.deal_time){
						self.filterData['deal_time'].push({value: key, label: data.deal_time[key]})
					}
					for (let key in data.perfection_degree){
						self.filterData['perfection_degree'].push({value: key, label: data.perfection_degree[key]})
					}
				}
			})

		},
		sortChange(column) {
			var self = this;
			var order = column.order == 'ascending'? order = 0: order = 1;
			self.customerParam.sort = column.prop;
			self.customerParam.order = order;
			self.loading = true;
			self.getCustomerList();
		},
		filterList(val, type) {
			var self = this;
			self.customerParam[type] = val;
			self.loading = true;
			self.getCustomerList();

		}
	},
	components: {

	}
}
</script>
<style lang="less">
	.container{
		min-width:1366px;
		overflow-x:auto;
	}
	.city-select-txt{
		font-size:14px;
		margin:10px 0;
	}
	.table-title{
		cursor:pointer;
	}
</style>



